<template>
  <div class="tool-bar-wrap">
    <div class="toolbar">
      <i class="el-icon-back header-btn" @click="undo" />
      <i class="el-icon-right header-btn" @click="redo" />
    </div>
    <div class="right-operation">
      <span class="next-page" @click="goBookPage">下一步</span>
      <van-icon name="ellipsis" class="header-btn more" @click="onMoreClick" />
      <i
        type="primary"
        class="el-icon-upload2 header-btn"
        @click="exportPic"
      ></i>
    </div>
    <!-- 预览 -->
    <Preview v-model="isShowPreview" @change="handlePreviewChange" />
    <van-popup v-model="showMore" position="bottom">
      <SizeForm @finish="showMore = false" />
    </van-popup>
  </div>
</template>

<script>
import generateID from "@/utils/generateID";
import toast from "@/utils/toast";
import { mapState } from "vuex";
import Preview from "@/components/Editor/Preview";
import { commonStyle, commonAttr } from "@/custom-component/component-list";
import eventBus from "@/utils/eventBus";
import { deepCopy } from "@/utils/utils";
import html2canvas from "html2canvas";
import SizeForm from "./SizeForm.vue";
import { Toast } from "vant";
import qs from "qs";

export default {
  components: { Preview, SizeForm },
  data() {
    return {
      isShowPreview: false,
      picName: "img-snapshoot",
      needToChange: [
        "top",
        "left",
        "width",
        "height",
        "fontSize",
        "borderWidth",
      ],
      scale: 22,
      timer: null,
      showMore: false,
    };
  },
  computed: mapState([
    "componentData",
    "canvasStyleData",
    "areaData",
    "curComponent",
    "prismRadio",
    "boxModelsData",
    "preBoxDetail",
  ]),
  created() {
    eventBus.$on("preview", this.preview);
    eventBus.$on("save", this.save);
    eventBus.$on("clearCanvas", this.clearCanvas);

    this.scale = this.canvasStyleData.scale;
  },
  mounted() {
    this.editorRef = document.getElementById("center");
    this.svgViewRef = document.getElementById("box-modle-wrap");
    this.editorViewRef = document.getElementById("editor");
  },
  methods: {
    format(value) {
      const scale = this.scale;
      return (value * parseInt(scale)) / 100;
    },

    getOriginStyle(value) {
      const scale = this.canvasStyleData.scale;
      const result = value / (parseInt(scale) / 100);
      return result;
    },

    handleScaleChange() {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        // 画布比例设一个最小值，不能为 0
        // eslint-disable-next-line no-bitwise
        this.scale = ~~this.scale || 1;
        const componentData = deepCopy(this.componentData);
        componentData.forEach((component) => {
          Object.keys(component.style).forEach((key) => {
            if (this.needToChange.includes(key)) {
              // 根据原来的比例获取样式原来的尺寸
              // 再用原来的尺寸 * 现在的比例得出新的尺寸
              // 不能用 Math.round 防止 1 px 的边框变 0
              component.style[key] = Math.ceil(
                this.format(this.getOriginStyle(component.style[key]))
              );
            }
          });
        });

        this.$store.commit("setComponentData", componentData);
        this.$store.commit("setCanvasStyle", {
          ...this.canvasStyleData,
          scale: this.scale,
        });
      }, 1000);
    },

    lock() {
      this.$store.commit("lock");
    },

    unlock() {
      this.$store.commit("unlock");
    },

    compose() {
      this.$store.commit("compose");
      this.$store.commit("recordSnapshot");
    },

    decompose() {
      this.$store.commit("decompose");
      this.$store.commit("recordSnapshot");
    },

    undo() {
      this.$store.commit("undo");
    },

    redo() {
      this.$store.commit("redo");
    },

    handleFileChange(e) {
      const file = e.target.files[0];
      if (!file.type.includes("image")) {
        toast("只能插入图片");
        return;
      }

      const reader = new FileReader();
      reader.onload = (res) => {
        const fileResult = res.target.result;
        const img = new Image();
        img.onload = () => {
          this.$store.commit("addComponent", {
            component: {
              ...commonAttr,
              id: generateID(),
              component: "Picture",
              label: "图片",
              icon: "",
              propValue: fileResult,
              style: {
                ...commonStyle,
                top: 0,
                left: 0,
                width: img.width,
                height: img.height,
              },
            },
          });

          this.$store.commit("recordSnapshot");

          // 修复重复上传同一文件，@change 不触发的问题
          document.querySelector("#input").setAttribute("type", "text");
          document.querySelector("#input").setAttribute("type", "file");
        };

        img.src = fileResult;
      };

      reader.readAsDataURL(file);
    },

    preview() {
      this.isShowPreview = true;
      this.$store.commit("setEditMode", "preview");
    },

    save() {
      localStorage.setItem("canvasData", JSON.stringify(this.componentData));
      localStorage.setItem("canvasStyle", JSON.stringify(this.canvasStyleData));
      Toast.success("保存成功");
    },

    clearCanvas() {
      this.$store.commit("setCurComponent", { component: null, index: null });
      this.$store.commit("setComponentData", []);
      this.$store.commit("recordSnapshot");
    },

    handlePreviewChange() {
      this.$store.commit("setEditMode", "edit");
    },

    // getStyle(obj, key) {
    //   return obj.currentStyle ? obj.currentStyle[key] : document.defaultView.getComputedStyle(obj, false)[key];
    // },

    async convertSvg2Canvas() {
      const svgElms = this.editorRef.getElementsByTagName("svg");
      const wrapEle = document.getElementById("content");
      html2canvas(wrapEle).then(function (canvas) {
        document.body.appendChild(canvas);
      });
    },

    async getPreviewImg() {
      await Promise.all([this.convertSvg2Canvas()]);
      const opts = {
        useCORS: true,
      };
      const canvasObj = await html2canvas(this.editorRef, opts);
      var context = canvasObj.getContext("2d");
      //防止图片模糊的设置
      context.mozImageSmoothingEnabled = false;
      context.webkitImageSmoothingEnabled = false;
      context.msImageSmoothingEnabled = false;
      context.imageSmoothingEnabled = false;
      // png 格式图片
      let imgType = "image/png";
      this.canvasSrc = canvasObj.toDataURL(imgType);
      this.previewShowFlag = true;
    },

    exportPic() {
      this.$store.commit("setCurComponent", { component: null, index: null });
      this.$nextTick(() => {
        html2canvas(this.editorRef).then(function (canvas) {
          const triggerDownload = document.createElement("a");
          triggerDownload.setAttribute("href", canvas.toDataURL());
          triggerDownload.setAttribute(
            "download",
            `下载图片${new Date().getTime()}.jpg`
          );
          triggerDownload.click();
        });

        html2canvas(this.svgViewRef).then(function (canvas) {
          const triggerDownload = document.createElement("a");
          triggerDownload.setAttribute("href", canvas.toDataURL());
          triggerDownload.setAttribute(
            "download",
            `下载图片底图${new Date().getTime()}.jpg`
          );
          triggerDownload.click();
        });

        html2canvas(this.editorViewRef).then(function (canvas) {
          const triggerDownload = document.createElement("a");
          triggerDownload.setAttribute("href", canvas.toDataURL());
          triggerDownload.setAttribute(
            "download",
            `下载图片其他${new Date().getTime()}.jpg`
          );
          triggerDownload.click();
        });
      });
    },

    dataURLToFile(dataURI, type) {
      var binary = atob(dataURI.split(",")[1]);
      var array = [];
      for (let i = 0; i < binary.length; i++) {
        array.push(binary.charCodeAt(i));
      }
      return new Blob([new Uint8Array(array)], { type: type });
    },

    upLoadImgFile(formData) {
      return fetch(
        "https://admin.wowzhixiang.com/addons/zjhj_mall/core/web/index.php?r=upload/save-img",
        {
          method: "POST",
          body: formData,
        }
      );
    },

    uploadPicToServer(ref, callback) {
      return new Promise((resolve, reject) => {
        const _this = this;
        if (ref) {
          html2canvas(ref).then(function (canvas) {
            const url = canvas.toDataURL();
            const formData = new FormData();
            const bolb = _this.dataURLToFile(url, "image/jpeg");
            const file = new File([bolb], new Date() + ".jpg");
            formData.append("file", file);
            return _this
              .upLoadImgFile(formData)
              .then((response) => response.text())
              .then((result) => {
                resolve(result);
              })
              .catch((error) => {
                reject(error);
              });
          });
        }
      });
    },

    goBookPage() {
      // const imgList = [
      //   'https://admin.wowzhixiang.com/addons/zjhj_mall/core/web/uploads/image/36/36f1b7a9525830ea9b7de5ee4ccc7767.jpg',
      //   "https://admin.wowzhixiang.com/addons/zjhj_mall/core/web/uploads/image/09/0966576aaab54e006231f5c30427ba88.jpg",
      //   "https://admin.wowzhixiang.com/addons/zjhj_mall/core/web/uploads/image/ba/ba4d37c536b2b9a390aa1a4d49828b5e.jpg"
      // ]
      const { id, access_token } = this.$route.query;
      if (id === undefined || access_token === undefined) {
        Toast.fail(`缺少参数 id 或 access_token ,无法下单`);
        return;
      }
      if (Object.keys(this.preBoxDetail).length === 0) {
        Toast.fail(`定制箱信息缺失，请检查后重新尝试`);
        return;
      }
      const { length, width, height, thickness } = this.boxModelsData;
      const promises = [
        this.editorRef,
        this.svgViewRef,
        this.editorViewRef,
      ].map((ref) => {
        return this.uploadPicToServer(ref);
      });
      Promise.all(promises)
        .then((res) => {
          const sendData = {
            content: JSON.stringify({
              ...this.preBoxDetail,
              L: +length,
              B: +width,
              H: +height,
              prismRadio: this.prismRadio,
              image: res[0],
              images: res,
              access_token: access_token,
              port: 3, //1：小程序，2：app，3：pc端
            }),
            id,
            access_token: access_token,
            port: 3, //1：小程序，2：app，3：pc端
          };
          if (id !== undefined) {
            sendData.id = id;
          }
          this.postCustomBoxInfo(sendData);
        })
        .catch((error) => {
          Toast.fail("图片上传失败！");
        });
    },

    postCustomBoxInfo(data) {
      fetch(
        "https://admin.wowzhixiang.com/addons/zjhj_mall/core/web/index.php?r=api/index/custom",
        {
          method: "POST",
          body: qs.stringify(data),
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
          },
        }
      )
        .then((res) => res.json())
        .then((res) => {
          console.log(res);
          if (res.code == 0) {
            console.log("goNextPage", res);
            this.goNextPage();
          } else {
            console.log(res.msg);
            Toast.fail(res.msg);
          }
        })
        .catch((err) => {
          Toast.fail("保存盒模型信息失败！");
        });
    },

    goNextPage() {
      try {
        wx.miniProgram.navigateTo({ url: "pages/home/index/index" });
      } catch (error) {
        Toast.success({
          message: "已下单，请手动返回上一页",
          position: "top",
        });
      }
      // if (wx) {
      //   wx.miniProgram.navigateTo({ url: "pages/home/index/index" });
      //   // wx.miniProgram.postMessage({ data: "foo" });
      // } else {
      //   Toast.success({
      //     message: "已下单，请手动返回上一页",
      //     position: "top",
      //   });
      // }
    },
    onMoreClick() {
      this.showMore = true;
    },
  },
};
</script>

<style lang="scss" scoped>
