  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 15px 10px;
  overflow-x: auto;
  background: #fff;
  border-bottom: 1px solid #ddd;

  position: relative;

  .header-btn {
    font-size: 20px;
  }

  .toolbar {
    white-space: nowrap;
    .el-icon-back,
    .el-icon-right {
      margin: 0 10px;
    }
  }
  .right-operation {
    position: absolute;
    right: 10px;
    display: flex;
    justify-content: space-between;
    .more {
      margin: 0 10px;
    }
    .next-page {
      background-color: #de7340;
      color: #fff;
      font-size: 12px;
      padding: 5px 10px;
      border-radius: 20px;
    }
  }
}
</style>
